import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component{
  state ={
    isLoading: true,
    movies: []
  }
  getMovies = async () => {
    const {data: {data: {movies}}} = await axios.get("https://yts.mx/api/v2/list_movies.json?sort_by:rating")
    this.setState({movies ,isLoading: false})
  }
  componentDidMount(){
    this.getMovies()
  }
  render(){
    const {isLoading, movies} = this.state
    return <section className="container">{isLoading ? <div className="loader"><span className="loader_text">Loading...</span></div> : <div className="movies">{movies.map((value) => <Movie key={value.id} id={value.id} title={value.title} year={value.year} summary={value.summary} poster={value.medium_cover_image} genres={value.genres}/>)}</div>}</section>
  }
}

export default App;
